* {
  box-sizing: border-box; }

html {
  color: #262525; }

.main {
  display: flex;
  flex-direction: column; }

ul {
  padding: 0;
  margin: 0; }

html {
  font-family: sans-serif;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

body {
  margin: 0; }

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
nav,
section,
summary {
  display: block; }

audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline; }

audio:not([controls]) {
  display: none;
  height: 0; }

[hidden],
template {
  display: none; }

a {
  background: transparent; }

a:active,
a:hover {
  outline: 0; }

abbr[title] {
  border-bottom: 1px dotted; }

b,
strong {
  font-weight: bold; }

dfn {
  font-style: italic; }

h1 {
  font-size: 2em;
  margin: 0.67em 0; }

mark {
  background: #ff0;
  color: #000; }

small {
  font-size: 80%; }

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline; }

sup {
  top: -0.5em; }

sub {
  bottom: -0.25em; }

img {
  border: 0; }

svg:not(:root) {
  overflow: hidden; }

figure {
  margin: 1em 40px; }

hr {
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  height: 0; }

pre {
  overflow: auto; }

code,
kbd,
pre,
samp {
  font-family: monospace, monospace;
  font-size: 1em; }

button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0; }

button {
  overflow: visible; }

button,
select {
  text-transform: none; }

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer; }

button[disabled],
html input[disabled] {
  cursor: default; }

button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0; }

input {
  line-height: normal; }

*
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  padding: 0; }

input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto; }

input[type="search"] {
  -webkit-appearance: textfield;
  -moz-box-sizing: content-box;
  -webkit-box-sizing: content-box;
  box-sizing: content-box; }

input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none; }

fieldset {
  border: 1px solid #c0c0c0;
  margin: 0 2px;
  padding: 0.35em 0.625em 0.75em; }

legend {
  border: 0;
  padding: 0; }

textarea {
  overflow: auto; }

optgroup {
  font-weight: bold; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

td,
th {
  padding: 0; }

.code {
  background-color: #c9c9c9;
  font-size: 1em;
  line-height: 1.6em;
  white-space: pre-wrap;
  word-wrap: break-word;
  padding: 1em; }

.note {
  font-style: italic;
  font-size: .9em;
  color: rgba(0, 0, 0, 0.5); }

.done {
  text-decoration: line-through; }

.warn {
  background-color: rgba(255, 50, 100, 0.5);
  color: rgba(0, 0, 0, 0.8); }

.hidden {
  display: none; }

.emphasis__state {
  color: rgba(0, 100, 100, 0.7); }

.emphasis__property {
  color: rgba(100, 0, 100, 0.7); }

html {
  background-color: #fff; }

body,
html {
  font-size: 18px;
  padding: 0;
  margin: 0;
  font-family: 'Source Sans Pro'; }

.primary {
  margin: 0;
  padding: 0; }

article {
  margin: 0 calc(0px - (100vw - 100%) / 2);
  padding-left: calc((100vw - 100%) / 2 + 20px);
  padding-right: calc((100vw - 100%) / 2 + 20px); }

.main__header {
  margin: 0;
  padding: 0 1em;
  color: #1c1c1c; }
  .main__header--large {
    text-align: center;
    color: #262525;
    font-size: 150%; }

.main__summary {
  padding: 0 1em; }

.main__footer {
  background-color: rgba(255, 255, 255, 0.2);
  padding: 1em; }

.bit__header h1 {
  color: #404040; }

.secondary {
  background-color: #cecece; }
  .secondary__header {
    background-color: rgba(0, 0, 255, 0.5); }

.alert {
  background-color: red;
  color: #fff;
  padding: 1em;
  margin: 0; }
  .alert__heading {
    background-color: #262525;
    padding: 0;
    margin: 0; }
  .alert__message {
    font-size: 1.5em;
    padding: 0;
    margin: 0; }
    .alert__message > * {
      margin: 0; }

.main-nav {
  width: 100vw;
  height: 20%;
  background-color: #c9c9c9; }

.main-nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center; }

@media screen and (min-width: 700px) {
  .main-nav-list {
    flex-direction: row; } }

.main-nav--item {
  list-style: none;
  padding: 30px; }
  .main-nav--item:hover {
    background-color: rgba(0, 0, 0, 0.2); }

.main-nav--item + .main-nav--item {
  margin: 0 10px; }

.main-nav--link {
  text-decoration: none;
  color: #262525;
  font-size: 20px; }

.main-nav--icon {
  width: 100px;
  height: 100px; }

h1 {
  text-align: center; }

.bit {
  padding: 1em;
  border-top: 1px solid rgba(0, 0, 0, 0.5); }
  .bit__header {
    font-size: .75em;
    color: #262525; }
  .bit__summary {
    color: #262525;
    letter-spacing: .25em;
    font-size: .9em; }

